  /* padding-left:6em; */
}

.proj-demo-thumb-container {
  /* border: 3px solid yellowgreen; */
  margin-top: 2em ;
  /* display:flex;
  flex-wrap: wrap; */
  padding:1em;
  position: relative;
  width: 30%;
}

.proj-demo-img{
  /* border: 3px solid blue; */
  opacity: 1;
  width:100%;
  /* height: 100%; */
  transition: .3s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  display: block;
  height:auto;
}

.proj-thumb-middle{
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transition: .3s ease;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
  /* below will overlap the text */
  width:100%;
  /* height:100%; */
  /* to place title in center */
  display:flex;
  justify-content: center; 
  align-items: center;
  height:auto;
}

.proj-demo-thumb-container:hover .proj-demo-img{
  opacity:0.5;
}


.proj-demo-thumb-container:hover .proj-thumb-middle{
  opacity: 1;
  content:"\A";
  width:100%;
  height:100%;
  background:rgba(251, 96, 96, 0.55);
  /* background:rgba(0, 255, 255, 0.5); */
    /* background:rgba(255, 0, 0, 0.5); */
}

